body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav-pills {
  background: black;
  min-height: 65px;
  color: #fff;
  font-size: 1.75em;
}
header {
  background-image: url('/assets/images/Cybertron-700x300.jpg');
  color: white;
}

h1 {
  font-weight: bold;
  font-size: 50px;
}

p {
  font-size: 24px;
  font-weight: bold;
}
.card {
  background: #fff;
  border-radius: 2px;
  height: 160px;
  overflow: hidden;
  margin: 1rem;
  position: relative;
  width: 245px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  display: inline-block;
}

.card > .img-container {
  width: 100%;
  height: auto;
  overflow: hidden;
}

.card > .img-container img {
  margin-top: 10px;
  margin: 10px;
  width: 91%;
  height: 140px;
  border-color: black;
  border-style: solid;
}
footer {
  background-color: black;
  height: 50px;
  position: relative;
  color: #fff;
  overflow: auto;
}
 
p {
  color: white;
  font-size: 16px;
  text-align: center;
  margin-top: 12px;
}
